.app {
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding: 50px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.app-counter {
  margin-bottom: 40px;
  line-height: calc(100vh - 300px);
  font-size: 80vh;
  color: black;
  text-align: center;
}
.app-counter--positive {
  color: blue;
}
.app-counter--negative {
  color: red;
}

.app-actions {
  height: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 90%;
}

.app-actions__buton { 
  -ms-flex: 1 1 0%; 
      flex: 1 1 0%;
  margin: 0 20px;
}



